import React from 'react';
import * as SC from './NavigationBar.styled';

export const NavigationBar = ({ direction }) => {
  return (
    <SC.NavbarContainer>
      <SC.NavbarList direction={direction}>
        <SC.NavbarLink to="/services"> </SC.NavbarLink>
        <SC.NavbarLink to="/info"> </SC.NavbarLink>
        <SC.NavbarLink to="/blog"></SC.NavbarLink>
      </SC.NavbarList>
    </SC.NavbarContainer>
  );
};
