import React from 'react';
import * as SC from './FormSection.styled';
import { useForm } from '../../hooks/UseForm/UseForm';
import { FcBusinessman, FcPhoneAndroid } from 'react-icons/fc';
import { MainButton } from '../Common/MainButton/MainButton';
import { MainInput } from '../Common/MainInput/MainInput';

export const FormSection = () => {
  const { handleInputChange, handleSubmit, fields, errors } = useForm();

  return (
    <SC.FormSection id="form-section">
      <SC.Title> </SC.Title>
      <SC.Form onSubmit={handleSubmit}>
        <MainInput
          onChange={handleInputChange}
          type="text"
          placeholder=" '"
          name="name"
          value={fields.name}
          id="input-name"
          error={errors.nameError}
          htmlFor="input-name"
          labelText=""
        >
          <FcBusinessman size={35} />
        </MainInput>
        <MainInput
          onChange={handleInputChange}
          type="tel"
          placeholder="  "
          name="phone"
          value={fields.phone}
          id="input-phone"
          error={errors.phoneError}
          htmlFor="input-phone"
          labelText=" "
        >
          <FcPhoneAndroid size={35} />
        </MainInput>

        <MainButton type="submit"> </MainButton>
      </SC.Form>
    </SC.FormSection>
  );
};
